<template>
   <group  class="jq-mt">
  <popup-picker :title="dateTile" :data="timeList" :value.sync="time" ></popup-picker>
   </group>

  <group  class="jq-mt">
    <div class="chart" v-epie="pieData" id="pieDataCost" ></div>
  </group>
  <group  class="jq-mt">
    <div class="chart" v-ebarline="barLineData" ></div>
  </group>
</template>

<script>

  import {Group,XButton,PopupPicker} from 'vux/src/components'
  import { getList } from '../../../untils/api'
  export default {
    components: {
    Group,
    PopupPicker,
    XButton
  },
  data () {
    return {
      pieData:{},
      dateTile:'日期',
      time: formatData.defaultTime,
      timeList: formatData.dateList,
      barLineData:{},
      //permissions:localStorage.getItem("permissions")

    }
  },
 events: {
  'eChartClick': function (data) {
      switch(data.id){
        case "pieDataCost":
         this.$route.router.go({name: 'coststructure',query: {bus: data.xValue}});
        break;
        default:
        break;
      }
  }
  },
  ready(){
   document.title='成本管理';
  },
  methods: {
    changeData(){
      window.location.reload();
    },
    async getData() {
      commit('UPDATE_LOADING', true)
      let id = "APP10107";
    let date=formatData.dateConversion(this.time);
   let dataTit = await getList(id,{date_:date});
      if(dataTit.success){
      
        //表头：
        //表格:
        const TableList = dataTit.data;
        this.pieData = {};
        this.pieData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
          commit('UPDATE_LOADING', false)
      }else{
        commit('UPDATE_LOADING', false)
        console.log(dataTit.message);
      }

    },
    async getBarLineData() {
    commit('UPDATE_LOADING', true)
    let id = "APP10111";
   let date=formatData.dateConversion(this.time);
   let dataTit = await getList(id,{date_:date});
      if(dataTit.success){
      
        //表头：
        //表格:
        const TableList = dataTit.data;
        this.barLineData = {};
        this.barLineData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
         commit('UPDATE_LOADING', false)
      }else{
        console.log(dataTit.message);
         commit('UPDATE_LOADING', false)
      }
    }
  },
     watch: {
     time (newtime, oldtime) {
      this.getData();
      this.getBarLineData();
       formatData.setDateTime(newtime);
    }
  },
  created: function () {
    this.pieData = {};
    this.pieData.dataset = [];
    this.barLineData = {};
    this.barLineData.dataset = [];
    this.getData();
    this.getBarLineData();
  }
  }

</script>






